
*{
	padding:0;
	margin:0;
}
html,body,#app{
	height:100%;
}
.send{
	background: #f0f2f8;
	height:100%;
}
.scroll{
	overflow: scroll;
	background: #f0f2f8;
	padding-bottom:220px;
	padding-top:120px;
	height:90%;
	box-sizing: border-box;
}
.s_header{
	background: #26b8f2;
	width: 100%;
	height: 100px;
	text-align: center;
	position: fixed;
	top:0;
}
.s_left{
	position: absolute;
	width:70px;
	height:70px;
	top:15px;
	left:20px;
}
.s_right{
	width:70px;
	height:70px;
	position: absolute;
	top:15px;
	right:20px;
}
.s_mid{
	padding-top:20px;
	font-size:40px;
	color:#fff;
}
.s_bottom{
	position: fixed;
	bottom:0;
	width:100%;
	height:100px;
	font-size:0;
	padding:0 2%;
}
.s_input{
	width:80%;
	height:100px;
	font-size:36px;
	background: #fff;
	border:none;
	border-radius: 5px;
}
.s_btn{
	display: inline-block;
	width:14%;
	color:#fff;
	font-size:34px;
	background: #dddee2;
	height:100%;
	vertical-align: top;
	margin-left:1.5%;
	text-align: center;
	line-height: 100px;
	border-radius: 20px;
}
.s_active{
	background: #10b5f6;
}
.s_my{
	text-align: right;
	margin-top:42px;
	margin-right:27px;
}
.my_avatar{
	width:120px;
	height:120px;
	vertical-align: middle;
}
.my_content{
	min-height: 100px;
	margin-right: 20px;
	background: #10b5f6;
	display: inline-block;
	vertical-align: top;
	color:#fff;
	font-size:40px;
	padding:30px 20px;
	border-radius:20px;
	box-sizing: border-box;
	max-width: 50%;
	white-space: normal;
	word-wrap:break-word ;
	text-align:left;
}
.s_yt{
	text-align: left;
	margin-top:42px;
	margin-left:27px;
}
.yt_content{
	margin-left: 20px;
	background: #10b5f6;
	display: inline-block;
	vertical-align: top;
	color:#fff;
	font-size:40px;
	padding:30px 20px;
	border-radius:20px;
	box-sizing: border-box;
	max-width: 50%;
	white-space: normal;
	word-wrap:break-word ;
	text-align:left;
}



